class Body extends React.Component {
  constructor() {
    super(...arguments);
    this.state = { opened: false, image: "", name: "", description: "" };
  }

  open = () => {
    this.setState({ opened: true });
  };

  close = () => {
    this.setState({ opened: false });
  };

  storeDetail = id => {
    this.props.stores.map(store => {
      if (store.id === id) {
        this.setState({
          name: store.name,
          image: store.image,
          description: store.description
        });
      }
    });
  };

  render() {
    const { image, name, description } = this.state;
    const { stores } = this.props;
    return (
      <React.Fragment>
        <h2 className="store-list-title">Store List</h2>
        <ul className="container">
          {stores.map(store => (
            <li onClick={this.open} key={store.id} className="item">
              <img
                src={store.thumb}
                onClick={() => this.storeDetail(store.id)}
                alt={`${store.name} image`}
              />
            </li>
          ))}
        </ul>
        {this.state.opened && (
          <Portal selector="#modal">
            <div className="overlay">
              <div className="modal">
                <img src={image} alt="store image" className="modal-image" />
                <h3>{name}</h3>
                <p>{description}</p>
                <button type="button" onClick={this.close}>
                  Close Modal
                </button>
              </div>
            </div>
          </Portal>
        )}
      </React.Fragment>
    );
  }
}

export default Body;
